<template>
    <div class="common-layout">
        <el-container class="home-container">
            <!-- 头部区域 -->
            <el-header>
                <div>
                    <img src="../assets/电商管理系统.jpeg" alt="">
                    <span>电商管理系统</span>
                </div>
                <el-button @click="logout" type="danger" >退出</el-button>
            </el-header>
            <!-- 页面主体区域 -->
            <el-container>
                <!-- 侧边栏 -->
                <el-aside width="auto">
                    <!-- 侧边栏菜单区域 -->
                    <el-menu active-text-color="#409eff" background-color="#333744"
                        text-color="#fff" unique-opened :collapse="isCollapse" router default-active="activeMenu">
                        <!-- 折叠控制按钮 -->
                        <div class="toggle-button" @click="toggleCollapse">|||</div>
                        <!-- 一级菜单 -->
                        <el-sub-menu :index="item.id + ''" v-for="item in menuList" :key="item.id">
                            <!-- 一级菜单的模板区域 -->
                            <template #title>
                                <!-- 图标 -->
                                <el-icon class="menu-icon">
                                    <component :is="iconObj[item.id]"></component>
                                </el-icon>
                                <!-- 文本 -->
                                <span>{{ item.authName }}</span>
                            </template>
                            <!-- 二级菜单 -->
                            <el-menu-item :index="'/home/' + subItem.path + ''" v-for="subItem in item.children" :key="subItem.id" @click="toggleHighLight">
                                <!-- 二级菜单的模板区域 -->
                                <template #title>
                                    <!-- 图标 -->
                                    <el-icon>
                                        <Menu />
                                    </el-icon>
                                    <!-- 文本 -->
                                    <span>{{ subItem.authName }}</span>
                                </template>
                            </el-menu-item>
                        </el-sub-menu>
                    </el-menu>
                </el-aside>
                <!-- 右侧内容主体 -->
                <el-main>
                    <RouterView></RouterView>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>
    
<script>
export default {
    data() {
        return {
            // 左侧菜单内容
            menuList: [],
            iconObj: {
                '125': "UserFilled",
                '103': "Grid",
                '101': "GoodsFilled",
                '102': "List",
                '145': "TrendCharts",
            },
            // 是否折叠左侧菜单
            isCollapse: false,
            // 高亮菜单
            activeMenu: ''
        }
    },
    methods: {
        logout() {
            // 1. 清除本地token
            window.sessionStorage.removeItem("token");
            // 2. 跳转回登陆页面
            this.$router.push('/login');
        },
        // 获取所有菜单
        async getMenuList() {
            const {data: res} = await this.$http.get('menus');
            if (res.meta.status !== 200) {
                return this.$message.error({message: res.meta.msg, duration: 1200});
            }
            this.menuList = res.data;
        },
        // 点击按钮,切换菜单的折叠与展开
        toggleCollapse() {
            this.isCollapse = !this.isCollapse;
        },
        // 点击菜单选项,这个选项就会高亮
        // 关于完整的高亮逻辑还有待完成.
        toggleHighLight(path) {
            this.activeMenu = path;
        }
    },
    created() {
        // 左侧菜单的内容根据用户权限不同而变化
        // 一进入页面时,就应该加载左侧菜单的内容
        this.getMenuList();
    }
}
</script>
    
<style lang="less" scoped>
// 对于elementui,每一个标签的名字就对应它的class
.common-layout {
    height: 100%;

    .home-container {
        height: 100%;
    }
}

.el-header {
    background-color: rgb(90, 192, 222);
    display: flex;
    justify-content: space-between;
    padding-left: 12px;
    align-items: center;
    color: #ffffff;
    font-size: 20px;

    >div {
        display: flex;
        align-items: center;

        img {
            width: 50px;
            height: 50px;
        }

        span {
            margin-left: 15px;
        }
    }
}

.el-aside {
    background-color: #333744;
    .toggle-button {
        height: 24px;
        background-color: #4a5064;
        text-align: center;
        color: #fff;
        line-height: 24px;
        letter-spacing: 0.2em;
        font-size: 10px;
        cursor: pointer;
    }
    .menu-icon {
        margin-right: 10px;
    }

    >.el-menu, >.el-sub-menu {
        border-right: 0;
        span {
            margin-right: 40px;
        }
    }
}

.el-main {
    background-color: #eaedf1;
}
</style>