<template>
    <div>
        <el-card>
            <el-row justify="space-between">
                <el-col :span="6"><span>欢迎您，{{ this.user.username }}</span></el-col>
                <el-col :span="6"><span>权限：{{ this.user.rname }}</span></el-col>
            </el-row>
            <div class="main">
                <div class="symbol system shadow-symbol">系统</div>
                <div :class="['symbol-surrounding', item.name]" v-for="(item, idx) in classList" :key="idx">
                    <div class="symbol active-symbol shadow-symbol">{{ item.value }}</div>
                </div>
            </div>
        </el-card>
    </div>
</template>
    
<script>
export default {
    data() {
        return {
            user: {},
            classList: [
                {name: 'users', value: '用户'}, 
                {name: 'rights', value: '权限'}, 
                {name: 'roles', value: '角色'}, 
                {name: 'goods', value: '商品'}, 
                {name: 'categories', value: '分类'}, 
                {name: 'params', value: '属性'}, 
                {name: 'orders', value: '订单'}, 
                {name: 'reports', value: '数据'}, 
            ],
            testString: '',
            // 圆圈的坐标
            coordinates: [
                { name: 'users', x: 180, y: 200 },
                { name: 'rights', x: 70, y: 270 },
                { name: 'roles', x: 70, y: 100 },
                { name: 'goods', x: 430, y: 200 },
                { name: 'categories', x: 530, y: 270 },
                { name: 'params', x: 530, y: 100 },
                { name: 'orders', x: 430, y: 380 },
                { name: 'reports', x: 180, y: 380 },
            ],
            // 圆圈之间的连线
            links: [
                { source: 'system', target: 'users', weight: null, color: null },
                { source: 'system', target: 'goods', weight: null, color: null },
                { source: 'system', target: 'reports', weight: null, color: null },
                { source: 'system', target: 'orders', weight: null, color: null },
                { source: 'users', target: 'roles', weight: null, color: null },
                { source: 'users', target: 'rights', weight: null, color: null },
                { source: 'roles', target: 'rights', weight: null, color: null },
                { source: 'goods', target: 'params', weight: null, color: null },
                { source: 'goods', target: 'categories', weight: null, color: null },
                { source: 'params', target: 'categories', weight: null, color: null },
            ],
            // 控制圆圈是否可以编辑
            editable: false,
        }
    },
    created() {
        if (!window.sessionStorage.getItem('user')) {
            this.$message.error({ message: '登陆失效，请重新登录！', duration: 1200 });
            this.$router.push('/login');
        }
        this.user = JSON.parse(window.sessionStorage.getItem('user'));
        this.$api.httpGet('roles/' + this.user.rid, {showOuter: false})
        .then( res => {
            if (res.meta.status === 200) {
                this.user.rname = res.data.roleName;
            } else {
                if (this.user.rid === 0) {
                    this.user.rname = '超级管理员'
                } else {
                    this.user.rname = '未知';
                }
            }
        })
        
        
    },
    mounted() {
        this.classList.forEach(item => {
            const ele = document.querySelector('.' + item.name);
            if (this.editable) {
                draggable(ele);
            }
            ele.addEventListener('click', () => {
                this.$router.push({name: item.name})
            })
            
        })
        // 配置圆圈坐标
        this.coordinates.forEach((item) => {
            const ele = document.querySelector('.' + item.name);
            ele.style.top = item.y - ele.offsetHeight / 2 + 'px';
            ele.style.left = item.x - ele.offsetWidth / 2 + 'px';
        })
        // 配置圆圈连线
        this.links.forEach(item => {
            if (!item.source || !item.target) return;
            const source = document.querySelector('.' + item.source);
            const target = document.querySelector('.' + item.target);
            const times = 10;
            const interval = 500 / times;
            let step = 1;
            let percent = step * (1 / times);
            let line = null;
            setTimeout(() => {
                const longer = setInterval(() => {
                    if (percent > 1) {
                        clearInterval(longer);
                        percent = 1;
                    }
                    line = drawLineByRotate(
                        getCenterOfBox(source).x,
                        getCenterOfBox(source).y,
                        getCenterOfBox(source).x + percent * (getCenterOfBox(target).x - getCenterOfBox(source).x),
                        getCenterOfBox(source).y + percent * (getCenterOfBox(target).y - getCenterOfBox(source).y),
                        { line: line, parent: document.querySelector('.main'), weight: item.weight, color: 'skyblue' }
                    );
                    step++;
                    percent = step * (1 / times);
                }, interval);
            }, 500);
        })
        // 禁止鼠标选中圆圈中的文字
        document.querySelectorAll('.symbol').forEach(item => {
            item.addEventListener('selectstart', function(e) {
                    e.preventDefault();
            })
        })
    },
    beforeUnmount() {
        this.classList.forEach(item => {
            const ele = document.querySelector('.' + item.name);
        })
    }
}
function draggable(element) {
    let drag = null;
    element.addEventListener('mousedown', function (mouse) {
        let mouseX = mouse.pageX - element.parentNode.offsetLeft;
        let mouseY = mouse.pageY - element.parentNode.offsetTop;
        this.offsetX = mouseX - element.offsetLeft;
        this.offsetY = mouseY - element.offsetTop;
        this.addEventListener('mousemove', drag = function (mouse) {
            let mouseX = mouse.pageX - element.parentNode.offsetLeft;
            let mouseY = mouse.pageY - element.parentNode.offsetTop;
            element.style.top = mouseY - element.offsetY + 'px';
            element.style.left = mouseX - element.offsetX + 'px';
        });
    });
    element.addEventListener('mouseup', function () {
        this.removeEventListener('mousemove', drag);
    });
    element.addEventListener('click', function () {
        this.removeEventListener('mousemove', drag);
    });
}
function drawLineBySVG(startX, startY, endX, endY, parent) {
    let [leftX, leftY, rightX, rightY] = [startX, startY, endX, endY];
    if (startX > endX) {
        [leftX, leftY, rightX, rightY] = [endX, endY, startX, startY];
    }
    const boxWidth = Math.abs(rightX - leftX);
    const boxHeight = Math.abs(rightY - leftY);
    const box = document.createElement('div');
    parent.appendChild(box);
    box.style.width = boxWidth + 'px';
    box.style.height = boxHeight + 'px';
    box.style.top = Math.min(leftY, rightY) + 'px';
    box.style.left = Math.min(leftX, rightX) + 'px';
    // 如何动态地获取data-v？
    box.setAttribute('data-v-34861546', true);
    // box.style.backgroundColor = 'gray';
    if (rightY > leftY) {
        box.className = 'line-down';
    } else {
        box.className = 'line-up';
    }
}
function drawLineByRotate(startX, startY, endX, endY, { line, parent, weight, color }) {
    if (!line) line = document.createElement('div');
    if (weight) line.style.borderWidth = weight + 'px';
    if (color) line.style.borderColor = color;

    line.className = 'rotate-line';
    line.setAttribute(document.querySelector('.main').attributes[0].name, true);

    line.style.top = startY + 'px';
    line.style.left = startX + 'px';
    line.style.width = Math.sqrt((endX - startX) * (endX - startX) + (endY - startY) * (endY - startY)) + 'px';
    let rotateDeg = Math.atan2(endY - startY, endX - startX) * 180 / Math.PI;
    line.style.transform = `rotate(${rotateDeg}deg)`;
    if (parent) {
        parent.appendChild(line);
    } else {
        document.body.appendChild(line);
    }
    return line;
}
function getCenterOfBox(box) {
    return {
        x: box.offsetLeft + box.offsetWidth / 2,
        y: box.offsetTop + box.offsetHeight / 2,
    };
}
</script>
    
<style lang="less" scoped>
@chart-width: 600px;
@chart-height: 500px;

.main {
    position: relative;
    width: @chart-width;
    height: @chart-height;
    margin: 25px auto;
    background-color: #fff;
}

.symbol {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: skyblue;
    font-size: 12px;
    line-height: 50px;
    text-align: center;
    cursor: default;
}

.symbol-surrounding {
    position: absolute;
    width: 64px;
    height: 64px;
    border: 3px solid skyblue;
    border-radius: 50%;
    z-index: 2;
    background-color: #fff;
    transition: transform 0.2s;
}
.symbol-surrounding:hover {
    transform: scale(1.1);
}
.system {
    top: (@chart-height / 2 - 40px);
    left: (@chart-width / 2 - 40px);
    transform: translate(0, 0);
    width: 80px;
    height: 80px;
    line-height: 80px;
    font-size: 14px;
    z-index: 2;
}

.shadow-symbol {
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.15);
}

.active-symbol {
    animation: grow .5s .3s ease forwards;
}

@keyframes grow {
    from {
        width: 50px;
        height: 50px;
        line-height: 50px;
        font-size: 12px;
    }

    to {
        width: 60px;
        height: 60px;
        line-height: 60px;
        font-size: 14px;
    }
}

.rotate-line {
    position: absolute;
    height: 0;
    border-width: 1px;
    border-style: solid;
    border-color: black;
    transform-origin: 0px;
    z-index: 1;
}

.line {
    position: absolute;
}

.line-up:extend(.line) {
    background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' version='1.1' preserveAspectRatio='none' viewBox='0 0 100 100'><line x1='0' y1='100' x2='100' y2='0' stroke='black' vector-effect='non-scaling-stroke'/></svg>");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100% 100%, auto;
    border-radius: 1px;
}

.line-down:extend(.line) {
    background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' version='1.1' preserveAspectRatio='none' viewBox='0 0 100 100'><line x1='0' y1='0' x2='100' y2='100' stroke='black' vector-effect='non-scaling-stroke'/></svg>");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100% 100%, auto;
    border-radius: 1px;
}
</style>