<template>
    <div class="login_container">
        <div class="login_box">
            <!-- 头像区域 -->
            <div class="avatar_box"><img src="../assets/logo.png" alt=""></div>
            <!-- 登陆表单区域 -->
            <el-form class="login_form" ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px"
                size="large">
                <!-- 用户名 -->
                <el-form-item prop="username">
                    <el-input v-model="loginForm.username">
                        <template #prefix>
                            <el-icon>
                                <User />
                            </el-icon>
                        </template>
                    </el-input>
                </el-form-item>
                <!-- 密码 -->
                <el-form-item prop="password">
                    <el-input show-password v-model="loginForm.password" @keydown.enter="login">
                        <template #prefix>
                            <el-icon>
                                <Lock />
                            </el-icon>
                        </template>
                    </el-input>
                </el-form-item>
                <el-form-item class="btns">
                    <el-button type="primary" @click="login">登录</el-button>
                    <el-button type="info" @click="resetLoginForm">重置</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>
        
<script>
// import { Message } from '@element-plus/icons-vue/dist/types';

export default {
    data() {
        return {
            // 登陆表单的数据绑定对象
            loginForm: {
                username: "admin",
                password: "123456"
            },
            // 登陆表单的验证规则对象
            loginFormRules: {
                // 用户名验证
                username: [
                    { required: true, message: '请输入登录名称', trigger: 'blur' },
                    { min: 3, max: 10, message: '用户名长度 3 到 10 个字符', trigger: 'blur' },
                ],
                // 密码验证
                password: [
                    { required: true, message: '请输入登录密码', trigger: 'blur' },
                    { min: 6, max: 20, message: '密码长度 6 到 20 个字符', trigger: 'blur' },
                ]
            }
        }
    },
    methods: {
        resetLoginForm() {
            this.$refs.loginFormRef.resetFields();
        },
        login() {
            this.$refs.loginFormRef.validate(async (vaild) => {
                if (!vaild) return;
                const { data: res } = await this.$http.request({
                    method: "post",
                    url: "login",
                    data: this.loginForm
                });
                if (res.meta.status !== 200) {
                    this.$message.error({ message: "登录失败", duration: 1200 });
                } else {
                    // 登录成功
                    // 1. 将token保存在客户端的sessionStorage中
                    // 1.1 项目中的其它接口必须在登录之后才能访问，即必须携带有效token
                    // 1.2 想要让登录只在当前打开的网站打开期间生效，将token存储在sessionStorage中
                    window.sessionStorage.setItem("token", res.data.token);
                    window.sessionStorage.setItem("user", JSON.stringify(res.data));
                    this.$router.push("/home");
                    // 2. 使用路由跳转到"/home"页面
                    this.$message.success({ message: "登录成功", duration: 1200 });
                }
            });
        }
    }
}

</script>
        
<style lang="less" scoped>
.login_container {
    height: 100%;
    background-color: #2b4b6b;
}

.login_box {
    width: 450px;
    height: 300px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    .avatar_box {
        position: absolute;
        left: 50%;
        transform: translate(-50%, -50%);
        height: 130px;
        width: 130px;
        padding: 10px;

        img {
            height: 100%;
            width: 100%;
            border-radius: 50%;
            background-color: #eee;
        }

        border: 1px solid #eee;
        border-radius: 50%;
        box-shadow: 0 0 10px #ddd;
        background-color: #fff;
    }
}

.login_form {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 10px;
    box-sizing: border-box;
}

.btns {
    justify-content: flex-end;

    /deep/ .el-form-item__content {
        flex: unset;
    }
}</style>